@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body.preload *{
  animation-duration: 0s !important;
  -webkit-animation-duration: 0s !important;
  transition:background-color 0s, opacity 0s, color 0s, width 0s, height 0s, padding 0s, margin 0s !important;}

body {
  font-family: 'Poppins', sans-serif;
  background: #fff;
}

.container {
  text-align: center;
  width: 100vw;
}


/* heading */


.resume {
  position: absolute;
  top: 30px;
  left: 20px;
}

.resume h3 {
  float: left;
  padding: 0px 15px;
  border-right: 3px solid #234ef3;
  font-size: 22px;
  transition: color 0.3s ease-in-out;
}

.resume h3:last-of-type {
  border-right: none;
}

.resume h3:last-of-type:hover {
  color: #9AB5FF;
  cursor: pointer;
}


/* Side Menu */

.side-menu {
  position: fixed;
  z-index: 1;
  width: 20%;
  height: 100%;

  transition: margin 0.5s, background-color 0.3s ease-in-out, border-right 0.3s ease-in-out;
  margin: 0 0 0 -14%;
  border: 3px solid rgba(0,0,0,0);
  pointer-events: none;
}

.side-menu:hover {
  background-color: rgb(249, 249, 249);
  border-right: 3px solid #9AB5FF;
}

.active-menu {
  margin-left: 0;
  background-color: rgb(249, 249, 249);
  opacity: 1;
  border-right: 3px solid #7c9fff;
  pointer-events: auto;
}

.menu-icon {
  position: relative;
  float:right;
  top: 70px;
  transition: color 0.3s ease-in-out;
  padding-right: 10px;
  pointer-events: auto;
}

.menu-icon:hover {
  color: #9AB5FF;
}

/* sidebar */

.sidebar {
  height: 100%;
  width: 70%;
  top: 0;
  left: 0;
  overflow-x: hidden;
  padding-top: 20px;
  float: left;
  opacity:0;
  transition: opacity 0.45s ease-in-out;
  border-right: 1px solid #fff;
}

.fade-in {
  opacity: 1;
}

.sidebar a {
  padding: 20px 10px 20px 10px;
  display: block;
  border-top: 3px solid #234ef3;
  text-decoration: none;
  color: black;
  transition: color 0.3s ease-in-out;
}

.sidebar a:hover {
  color: #9AB5FF;
}

.sidebar h1 {
  font-size: 2.5vw;
  padding-bottom: 20px;
  text-align: center;
}

.sidebar a:last-of-type {
  border-bottom: none;
}

/* cover page */
.cover-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
}

.cover {
  max-width: 800px;
}

.cover img{
  max-height: 50vh;
  border-radius: 50%;
  border: 2px solid grey;
  box-shadow: 0px 0px 10px 0px rgb(0 0 0 / 18%);
  transition: 0.3s all ease-in-out;
}

.cover img:hover {
  transform: scale(0.99);
}

.cover h1 {
  padding: 2% 0 2% 0;
  font-size: 5.9vh;
}

.animated {
  animation: bounce 0.8s infinite alternate;
  -webkit-animation: bounce 0.8s infinite alternate;
}

@keyframes bounce {
  from {
    transform: translateY(5px);
  }
  to {
    transform: translateY(-15px);
  }
}
@-webkit-keyframes bounce {
  from {
    transform: translateY(5px);
  }
  to {
    transform: translateY(-15px);
  }
}

.content {
  max-width: 800px;
  width: auto;
  height: auto;
  margin: 0 auto;
  position: relative;
}

/* About me */
.about-me {
  width: auto;
  height: auto;
  margin: 0 auto;
  padding: 10px;
  position: relative;
}

.heading {
  padding-top: 10%;
  padding-bottom: 2%;
  font-size: 2.5em;
}

.about-me-content {
  height: auto;
  clear: both;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.about-me-img {
  width: 40%;
  height: auto;
  float: left;
  padding-right: 20px;
  
}

.about-me-img img {
  max-width: 100%;
  border-radius: 5px;
  transition: 0.3s all ease-in-out;
}

.about-me-img img:hover {
  transform: scale(0.99);
}

.about-me-img p{
  font-style: italic;
  font-size: 0.7em;
}

.about-me-text {
  width:60%;
  float:right;
  padding: 10px;
}

/* Project Template 1 */
.project-template-1 {
  position: relative;
  display: inline-block;
  border: 1px solid black;
  background-color: blanchedalmond;
  border-radius: 20px;
  margin: 0;
  width: 100%;
}

.project-icons {
  position: absolute;
  right: -60px;
  top: 50px;
  width: 60px;
  display: block;
  border: 1px solid black;
  border-left: none;
  background-color: blanchedalmond;
  border-radius: 10px; 
}

.close-icon p {
  color: black;
  margin-top: 10px;
  transition: color 0.3s ease-in-out
}

.close-icon p:hover {
  color: rgb(236, 56, 56);
}

.project-github p {
  color: black;
  padding-bottom: 20px;
  transition: color 0.3s ease-in-out;
}

.project-github p:hover {
  color:rgb(0, 174, 255);
}

.project-contents {
  display: inline-block;
  margin: 10px 60px 10px 60px;
  max-height: 80vh;
  overflow: auto;
}

.project-template-1 h3 {
  border: 1px solid black;
  border-radius: 20px;
  padding: 10px 10px;
  margin: 10px 0;
  font-size: 1.5em;
  width: 100%;
}

.project-template-1 p {
  padding-bottom: 10px;
}

.project-template-1 img {
  max-width: 100%;
  max-height: 50vh;
}

.image-contents {
  text-align: center;
  padding: 10px 0;
}

.caption-block {
  padding: 5px 0;
}

.caption {
  border: none;
  padding: 10px 20px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 16px;
}

.caption a {
  text-decoration: none;
}

/* Socials */

.socials {
  display: inline-block;
  width: 100%;
}

.social-icons {
  padding: 10px 0px;
}

.social-icons a {
  padding-right: 50px;
  color: black;
}

.social-icons a:last-of-type {
  padding-right: 0px;
}

.social-icons a:hover {
  color: rgb(0, 174, 255);
}

/* Project template 2 */

.project-summary {
  color: #fff;
  text-align: left;
  margin: 10px 10px;
  transition: 0.3s all ease-in-out;
  border: 0.1px solid black;
  box-shadow: 0px 0px 15px 0px rgb(0 0 0 / 50%)
}

.project-summary:hover {
  cursor: pointer;
  transform: scale(1.01);
}

.project-summary h2 {
  font-size: 20px;
  padding-bottom: 10px;
}

.project-summary p {
  padding-top: 5px;
  font-size: 16px;
}

.tint {
  background-color: rgba(0, 0, 0, 0.6);
  padding: 30px 20px;
}

.popup {
  position: fixed;
  width: 100vw;
  height: 120vh;
  top:-20vh;
  left:0;
  background: rgba(0,0,0,0.5);
}

.project-container {
  position: relative;
  top: 70vh;
  left: 50vw;
  transform: translate(-50%, -50%);
  width: 800px; 
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

}


.project-open {
  display: block;
  animation: open-project 1s ease-in-out forwards;
  transform-origin: top center;
}

.project-close {
  display: block;
  animation: close-project 0.6s ease-in-out forwards;
  animation-direction: reverse;
  transform-origin: top center;
}

@keyframes open-project {
  0% {
    margin-top: -50%;
    transform: scaleY(0);
    opacity: 0%;
  }

  80% {
    transform: scaleY(1.03);
    margin-top: 10px;
    opacity: 80%;
  }

  100% {
    margin-top: 0%;
    transform: scaleY(1);
    opacity: 100%;
  }
}

@keyframes close-project {
  0% {
    margin-top: -50%;
    transform: scaleY(0);
    opacity: 0%;
  }

  80% {
    transform: scaleY(1.03);
    margin-top: 10px;
    opacity: 80%;
  }

  100% {
    margin-top: 0%;
    transform: scaleY(1);
    opacity: 100%;
  }
}